import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TasksService, CreateTaskRequest, UpdateTaskRequest } from '../../../services/tasks.service';
import { TranslationService } from '../../../services/translation.service';
import { LangService } from '../../../services/lang.service';
import { ConfirmationModalComponent } from '../../shared/confirmation-modal/confirmation-modal.component';
import { Task, TasksRoot } from '../../../models/tasks.model';
import { UsersService } from '../../../services/users.service';

@Component({
  selector: 'app-all-tasks',
  standalone: false,
  templateUrl: './all-tasks.component.html',
  styleUrls: ['./all-tasks.component.css']
})
export class AllTasksComponent implements OnInit {
  @ViewChild('confirmationModal') confirmationModal!: ConfirmationModalComponent;

  // Data
  tasks: Task[] = [];
  
  // Language
  currentLang = 'en';
  
  // UI state
  isLoading = false;
  
  // Pagination state
  currentPage = 1;
  pageSize = 10;
  totalCount = 0;
  totalPages = 1;
  pageSizeOptions = [5, 10, 15, 20];
  
  // Filters
  filterForm!: FormGroup;
  
  // Tab state
  selectedStatus: number | null = null;
  allTasksForCounting: Task[] = []; // Store all tasks for counting by status
  
  // Modal visibility
  showAddTaskModal = false;
  showEditTaskModal = false;
  showTaskDetailsModal = false;
  
  // Current task and details
  currentTask: Task | null = null;
  currentTaskDetails: Task | null = null;
  
  // Forms
  addTaskForm!: FormGroup;
  editTaskForm!: FormGroup;
  
  // Users for dropdowns
  users: Array<{ id: string; name: string; departmentName: string }> = [];
  
  // Status mapping
  readonly STATUS_MAP = {
    0: 'new',
    1: 'inProgress', 
    2: 'done',
    3: 'stopped',
    4: 'late',
    5: 'returned',
    6: 'review'
  };
  
  readonly PRIORITY_MAP = {
    1: 'high',
    2: 'medium', 
    3: 'low'
  };

  constructor(
    private tasksService: TasksService,
    private usersService: UsersService,
    private translationService: TranslationService,
    private langService: LangService,
    private fb: FormBuilder,
    private cdr: ChangeDetectorRef
  ) {
    this.initializeForms();
  }

  ngOnInit() {
    this.loadData();
    this.langService.currentLang$.subscribe(lang => {
      this.currentLang = lang;
      this.cdr.detectChanges();
    });
  }

  private initializeForms() {
    this.filterForm = this.fb.group({
      status: [''],
      dueDateFrom: [''],
      dueDateTo: ['']
    });

    this.addTaskForm = this.fb.group({
      title: ['', [Validators.required, Validators.maxLength(30)]],
      description: [''],
      assigneeUserId: ['', Validators.required],
      priority: ['', Validators.required],
      dueDate: ['', Validators.required],
      sourceType: [0]
    });

    this.editTaskForm = this.fb.group({
      title: ['', [Validators.required, Validators.maxLength(40)]],
      description: [''],
      assigneeUserId: ['', Validators.required],
      priority: ['', Validators.required],
      dueDate: ['', Validators.required],
      sourceType: [0]
    });
  }

  private async loadData() {
    this.isLoading = true;
    try {
      await Promise.all([
        this.loadAllTasks(),
        this.loadUsers()
      ]);
    } catch (error) {
      console.error('Error loading data:', error);
    } finally {
      this.isLoading = false;
    }
  }

  private async loadUsers() {
    return new Promise<void>((resolve, reject) => {
      this.usersService.getUserNames().subscribe({
        next: (users) => {
          this.users = users;
          resolve();
        },
        error: reject
      });
    });
  }

  private async loadAllTasks() {
    return new Promise<void>((resolve, reject) => {
      const formValue = this.filterForm.value;
      
      // Use selectedStatus from tab if available, otherwise use form status
      const statusFilter = this.selectedStatus !== null ? this.selectedStatus : (formValue.status || undefined);
      
      this.tasksService.getAllTasksWithFilters(
        this.currentPage,
        this.pageSize,
        statusFilter,
        formValue.dueDateFrom || undefined,
        formValue.dueDateTo || undefined
      ).subscribe({
        next: (response) => {
          if (response.success && response.data?.items) {
            this.tasks = response.data.items;
            this.totalCount = response.data.totalCount;
            this.totalPages = Math.ceil(this.totalCount / this.pageSize);
            
            // Load all tasks for counting (first page with large page size)
            this.loadAllTasksForCounting();
          }
          resolve();
        },
        error: reject
      });
    });
  }

  private async loadAllTasksForCounting() {
    return new Promise<void>((resolve, reject) => {
      const formValue = this.filterForm.value;
      
      this.tasksService.getAllTasksWithFilters(
        1,
        1000, // Large page size to get all tasks
        undefined, // No status filter for counting
        formValue.dueDateFrom || undefined,
        formValue.dueDateTo || undefined
      ).subscribe({
        next: (response) => {
          if (response.success && response.data?.items) {
            this.allTasksForCounting = response.data.items;
          }
          resolve();
        },
        error: reject
      });
    });
  }

  // Filter methods
  onFilterChange() {
    this.currentPage = 1; // Reset to first page when filtering
    this.selectedStatus = null; // Reset tab selection when using filters
    this.loadData();
  }

  clearFilters() {
    this.filterForm.reset();
    this.selectedStatus = null; // Reset tab selection
    this.currentPage = 1;
    this.loadData();
  }

  // Tab methods
  onStatusTabClick(status: number | null) {
    this.selectedStatus = status;
    this.currentPage = 1; // Reset to first page when changing tabs
    this.loadData();
  }

  getStatusCount(status: number | null): number {
    if (status === null) {
      return this.allTasksForCounting.length;
    }
    return this.allTasksForCounting.filter(task => task.status === status).length;
  }

  // Pagination methods
  goToPage(page: number) {
    if (page >= 1 && page <= this.totalPages && page !== this.currentPage) {
      this.currentPage = page;
      this.loadData();
    }
  }

  changePageSize(pageSize: number) {
    this.pageSize = pageSize;
    this.currentPage = 1;
    this.loadData();
  }

  getPageNumbers(): number[] {
    const pages: number[] = [];
    const startPage = Math.max(1, this.currentPage - 2);
    const endPage = Math.min(this.totalPages, this.currentPage + 2);
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    return pages;
  }

  getStartIndex(): number {
    return (this.currentPage - 1) * this.pageSize + 1;
  }

  getEndIndex(): number {
    return Math.min(this.currentPage * this.pageSize, this.totalCount);
  }

  // Utility methods
  translate(key: string): string {
    return this.translationService.translate(key);
  }

  isRTL(): boolean {
    return this.langService.isRTL();
  }

  getStatusClass(status: number): string {
    const statusKey = this.STATUS_MAP[status as keyof typeof this.STATUS_MAP];
    return statusKey ? `status-${statusKey}` : '';
  }

  getPriorityClass(priority: number): string {
    const priorityKey = this.PRIORITY_MAP[priority as keyof typeof this.PRIORITY_MAP];
    return priorityKey ? `priority-${priorityKey}` : '';
  }

  getPriorityText(priority: number): string {
    const priorityKey = this.PRIORITY_MAP[priority as keyof typeof this.PRIORITY_MAP];
    return priorityKey ? this.translate(`tasks.priority.${priorityKey}`) : '';
  }

  getStatusText(status: number): string {
    const statusKey = this.STATUS_MAP[status as keyof typeof this.STATUS_MAP];
    return statusKey ? this.translate(`tasks.status.${statusKey}`) : '';
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString(this.langService.getCurrentLang() === 'ar' ? 'ar-EG' : 'en-US');
  }

  formatDateTime(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleString(this.langService.getCurrentLang() === 'ar' ? 'ar-EG' : 'en-US');
  }

  // Get status options for filter dropdown
  getStatusOptions() {
    return [
      { value: '', text: this.translate('tasks.all_tasks.all_statuses') },
      { value: 0, text: this.translate('tasks.status.new') },
      { value: 1, text: this.translate('tasks.status.inProgress') },
      { value: 2, text: this.translate('tasks.status.done') },
      { value: 3, text: this.translate('tasks.status.stopped') },
      { value: 4, text: this.translate('tasks.status.late') },
      { value: 5, text: this.translate('tasks.status.returned') },
      { value: 6, text: this.translate('tasks.status.review') }
    ];
  }

  // Modal management methods
  openAddTaskModal() {
    this.closeAllModals();
    this.showAddTaskModal = true;
    this.addTaskForm.reset();
    this.updateBodyClass();
  }

  openEditTaskModal(task: Task) {
    this.closeAllModals();
    this.currentTask = task;
    this.showEditTaskModal = true;
    this.editTaskForm.patchValue({
      title: task.title,
      description: task.description,
      assigneeUserId: task.assigneeUserId,
      priority: task.priority,
      dueDate: task.dueDate.split('T')[0], // Format for date input
      sourceType: task.sourceType
    });
    this.updateBodyClass();
  }

  openTaskDetailsModal(task: Task) {
    this.closeAllModals();
    this.currentTask = task;
    this.showTaskDetailsModal = true;
    this.loadTaskDetails(task.id);
    this.updateBodyClass();
  }

  closeAllModals() {
    this.showAddTaskModal = false;
    this.showEditTaskModal = false;
    this.showTaskDetailsModal = false;
    this.updateBodyClass();
  }

  private updateBodyClass() {
    const hasOpenModal = this.showAddTaskModal || this.showEditTaskModal || this.showTaskDetailsModal;
    
    if (hasOpenModal) {
      document.body.classList.add('modal-open');
    } else {
      document.body.classList.remove('modal-open');
    }
  }

  private async loadTaskDetails(taskId: number) {
    return new Promise<void>((resolve, reject) => {
      this.tasksService.getTaskById(taskId).subscribe({
        next: (response) => {
          if (response.success && response.data) {
            this.currentTaskDetails = response.data;
          }
          resolve();
        },
        error: reject
      });
    });
  }

  // Form submissions
  async onSubmitAddTask() {
    if (this.addTaskForm.valid) {
      try {
        const formValue = this.addTaskForm.value;
        const taskData: CreateTaskRequest = {
          title: formValue.title,
          description: formValue.description,
          assigneeUserId: formValue.assigneeUserId,
          priority: formValue.priority,
          dueDate: new Date(formValue.dueDate).toISOString(),
          sourceType: formValue.sourceType
        };

        const response = await this.tasksService.createTask(taskData).toPromise();
        
        if (response && response.success) {
          this.closeAllModals();
          this.loadData();
          this.showSuccessMessage(this.translate('tasks.add_task_success'));
        }
      } catch (error) {
        console.error('Error creating task:', error);
        this.showErrorMessage(this.translate('tasks.add_task_error'));
      }
    }
  }

  async onSubmitEditTask() {
    if (this.editTaskForm.valid && this.currentTask) {
      try {
        const formValue = this.editTaskForm.value;
        const taskData: UpdateTaskRequest = {
          title: formValue.title,
          description: formValue.description,
          assigneeUserId: formValue.assigneeUserId,
          priority: formValue.priority,
          dueDate: new Date(formValue.dueDate).toISOString(),
          sourceType: formValue.sourceType
        };

        const response = await this.tasksService.updateTask(this.currentTask.id, taskData).toPromise();
        
        if (response && response.success) {
          this.closeAllModals();
          this.loadData();
          this.showSuccessMessage(this.translate('tasks.edit_task_success'));
        }
      } catch (error) {
        console.error('Error updating task:', error);
        this.showErrorMessage(this.translate('tasks.edit_task_error'));
      }
    }
  }

  async deleteTask(task: Task) {
    if (this.confirmationModal) {
      const modalData = {
        title: this.translate('tasks.delete_task'),
        message: this.translate('tasks.confirm_delete_task').replace('{name}', task.title),
        confirmText: this.translate('common.delete'),
        cancelText: this.translate('common.cancel'),
        type: 'delete' as const
      };
      
      this.confirmationModal.show(modalData);
      
      const subscription = this.confirmationModal.confirm.subscribe(async () => {
        try {
          const response = await this.tasksService.deleteTask(task.id).toPromise();
          
          if (response && response.success) {
            this.loadData();
            this.showSuccessMessage(this.translate('tasks.delete_task_success'));
          }
        } catch (error) {
          console.error('Error deleting task:', error);
          this.showErrorMessage(this.translate('tasks.delete_task_error'));
        }
        
        subscription.unsubscribe();
      });
    }
  }

  // Utility methods for messages
  showSuccessMessage(message: string) {
    console.log('Success:', message);
    // You can implement toast notifications here
  }

  showErrorMessage(message: string) {
    console.log('Error:', message);
    // You can implement toast notifications here
  }
}
