import { Component, OnInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { TasksService, TaskFeedbackRequest } from '../../../services/tasks.service';
import { TranslationService } from '../../../services/translation.service';
import { LangService } from '../../../services/lang.service';
import { ConfirmationModalComponent } from '../../shared/confirmation-modal/confirmation-modal.component';
import { Task, TasksRoot, TaskFeedback } from '../../../models/tasks.model';

@Component({
  selector: 'app-my-tasks',
  standalone: false,
  templateUrl: './my-tasks.component.html',
  styleUrls: ['./my-tasks.component.css']
})
export class MyTasksComponent implements OnInit {
  @ViewChild('confirmationModal') confirmationModal!: ConfirmationModalComponent;

  // Data
  tasks: Task[] = [];
  allTasksForCounting: Task[] = []; // Store all tasks for status counting
  
  // Language
  currentLang = 'en';
  
  // UI state
  isLoading = false;
  
  // Pagination state
  currentPage = 1;
  pageSize = 5; // 5 tasks per column per page
  totalCount = 0;
  totalPages = 1;
  
  // Modal visibility
  showFeedbackModal = false;
  showTaskDetailsModal = false;
  
  // Current task and details
  currentTask: Task | null = null;
  currentTaskDetails: Task | null = null;
  currentTaskFeedback: TaskFeedback[] = [];
  
  // Forms
  feedbackForm!: FormGroup;
  
  // Status mapping
  readonly STATUS_MAP = {
    0: 'new',
    1: 'inProgress', 
    2: 'done',
    3: 'stopped',
    4: 'late',
    5: 'returned',
    6: 'review'
  };
  
  readonly PRIORITY_MAP = {
    1: 'high',
    2: 'medium', 
    3: 'low'
  };

  constructor(
    private tasksService: TasksService,
    private translationService: TranslationService,
    private langService: LangService,
    private fb: FormBuilder,
    private cdr: ChangeDetectorRef
  ) {
    this.initializeForms();
  }

  ngOnInit() {
    this.loadData();
    this.langService.currentLang$.subscribe(lang => {
      this.currentLang = lang;
      this.cdr.detectChanges();
    });
  }

  private initializeForms() {
    this.feedbackForm = this.fb.group({
      feedbackText: ['', [Validators.minLength(10)]]
    });
  }

  private async loadData() {
    this.isLoading = true;
    try {
      await this.loadMyTasks();
    } catch (error) {
      console.error('Error loading data:', error);
    } finally {
      this.isLoading = false;
    }
  }

  private async loadMyTasks() {
    return new Promise<void>((resolve, reject) => {
      this.tasksService.getEmployeeTasks().subscribe({
        next: (response) => {
          if (response.success && response.data?.items) {
            // Filter tasks to only show New, In_Progress, and returned statuses
            this.tasks = response.data.items.filter(task => 
              task.status === 0 || task.status === 1 || task.status === 5
            );
            this.allTasksForCounting = this.tasks;
            this.totalCount = this.tasks.length;
            this.totalPages = Math.ceil(this.totalCount / this.pageSize);
          }
          resolve();
        },
        error: reject
      });
    });
  }

  // Modal management
  private closeAllModals() {
    this.showFeedbackModal = false;
    this.showTaskDetailsModal = false;
    this.updateBodyClass();
  }

  private canOpenModal(): boolean {
    return !this.showFeedbackModal && !this.showTaskDetailsModal;
  }

  private updateBodyClass() {
    const hasOpenModal = this.showFeedbackModal || this.showTaskDetailsModal;
    
    if (hasOpenModal) {
      document.body.classList.add('modal-open');
    } else {
      document.body.classList.remove('modal-open');
    }
  }

  // Task actions
  openTaskDetailsModal(task: Task) {
    if (this.canOpenModal()) {
      this.closeAllModals();
      this.currentTask = task;
      this.showTaskDetailsModal = true;
      this.loadTaskFeedback(task.id);
      this.updateBodyClass();
    }
  }

  openFeedbackModal(task: Task) {
    // Close all modals first, then open feedback modal
    this.closeAllModals();
    this.currentTask = task;
    this.showFeedbackModal = true;
    this.feedbackForm.reset();
    this.updateBodyClass();
  }

  private async loadTaskFeedback(taskId: number) {
    return new Promise<void>((resolve, reject) => {
      this.tasksService.getTaskFeedback(taskId).subscribe({
        next: (response) => {
          if (response.success && response.data) {
            this.currentTaskFeedback = response.data;
          }
          resolve();
        },
        error: reject
      });
    });
  }

  // Form submissions
  async onSubmitFeedback() {
    if (this.feedbackForm.valid && this.currentTask) {
      try {
        const formValue = this.feedbackForm.value;
        
        const feedbackData: TaskFeedbackRequest = {
          feedbackText: formValue.feedbackText
        };
        
        const response = await this.tasksService.submitTaskFeedback(this.currentTask.id, feedbackData).toPromise();
        
        if (response && response.success) {
          await this.loadMyTasks();
          this.closeModal('showFeedbackModal');
          
          // Show success confirmation
          if (this.confirmationModal) {
            const modalData = {
              title: this.translate('tasks.myTasks.dialogs.feedbackSuccess'),
              message: this.translate('tasks.myTasks.dialogs.feedbackSuccess'),
              confirmText: this.translate('common.ok'),
              cancelText: this.translate('common.close'),
              type: 'info' as const
            };
            this.confirmationModal.show(modalData);
            
            // Handle the confirmation
            const subscription = this.confirmationModal.confirm.subscribe(() => {
              subscription.unsubscribe();
            });
          }
        }
      } catch (error) {
        console.error('Error submitting feedback:', error);
        this.showErrorMessage(this.translate('tasks.myTasks.dialogs.feedbackError'));
      }
    }
  }

  // Utility methods
  translate(key: string): string {
    return this.translationService.translate(key);
  }

  // Message display methods
  showSuccessMessage(message: string) {
    console.log('Success:', message);
  }

  showErrorMessage(message: string) {
    console.log('Error:', message);
  }

  isRTL(): boolean {
    return this.langService.isRTL();
  }

  getStatusClass(status: number): string {
    const statusKey = this.STATUS_MAP[status as keyof typeof this.STATUS_MAP];
    return statusKey ? `status-${statusKey}` : '';
  }

  getPriorityClass(priority: number): string {
    const priorityKey = this.PRIORITY_MAP[priority as keyof typeof this.PRIORITY_MAP];
    return priorityKey ? `priority-${priorityKey}` : '';
  }

  getPriorityText(priority: number): string {
    const priorityKey = this.PRIORITY_MAP[priority as keyof typeof this.PRIORITY_MAP];
    return priorityKey ? this.translate(`tasks.priority.${priorityKey}`) : '';
  }

  getStatusText(status: number): string {
    const statusKey = this.STATUS_MAP[status as keyof typeof this.STATUS_MAP];
    return statusKey ? this.translate(`tasks.status.${statusKey}`) : '';
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString(this.langService.getCurrentLang() === 'ar' ? 'ar-EG' : 'en-US');
  }

  // Close modals
  closeModal(modalName: string) {
    switch (modalName) {
      case 'showFeedbackModal':
        this.showFeedbackModal = false;
        this.feedbackForm.reset();
        break;
      case 'showTaskDetailsModal':
        this.showTaskDetailsModal = false;
        this.currentTask = null;
        this.currentTaskDetails = null;
        this.currentTaskFeedback = [];
        break;
    }
    
    this.updateBodyClass();
  }

  // Check if task can have feedback submitted
  canSubmitFeedback(task: Task): boolean {
    return task.status === 0 || task.status === 1 || task.status === 5;
  }

  // Get tasks by status for display with pagination
  getTasksByStatus(status: number): Task[] {
    // Get all tasks of this status from all loaded data
    const allStatusTasks = this.allTasksForCounting.filter(task => task.status === status);
    
    // Calculate pagination for this specific status
    const tasksPerPage = 5; // 5 tasks per column per page
    const startIndex = (this.currentPage - 1) * tasksPerPage;
    const endIndex = startIndex + tasksPerPage;
    
    // Return the tasks for this status on the current page
    return allStatusTasks.slice(startIndex, endIndex);
  }

  getStatusTaskCount(status: number): number {
    return this.allTasksForCounting.filter(task => task.status === status).length;
  }

  // Pagination methods
  goToPage(page: number) {
    if (page >= 1 && page <= this.getMaxPagesForAnyStatus() && page !== this.currentPage) {
      this.currentPage = page;
      // No need to reload from server, just update the display
    }
  }
  
  getMaxPagesForAnyStatus(): number {
    // Find the status with the most tasks to determine total pages
    const maxTasks = Math.max(
      this.getStatusTaskCount(0), // New
      this.getStatusTaskCount(1), // In Progress
      this.getStatusTaskCount(5)  // Returned
    );
    
    return Math.ceil(maxTasks / 5); // 5 tasks per column per page
  }

  getPageNumbers(): number[] {
    const pages: number[] = [];
    const maxPages = this.getMaxPagesForAnyStatus();
    const startPage = Math.max(1, this.currentPage - 2);
    const endPage = Math.min(maxPages, this.currentPage + 2);
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    return pages;
  }

  getStartIndex(): number {
    return (this.currentPage - 1) * 5 + 1; // 5 tasks per column per page
  }

  getEndIndex(): number {
    const maxTasks = Math.max(
      this.getStatusTaskCount(0),
      this.getStatusTaskCount(1),
      this.getStatusTaskCount(5)
    );
    return Math.min(this.currentPage * 5, maxTasks);
  }

  getTotalTasks(): number {
    return this.totalCount;
  }

  getStatusTasksOnOtherPages(status: number): number {
    // Calculate how many tasks of this status are on other pages
    const totalForStatus = this.getStatusTaskCount(status);
    const onCurrentPage = this.tasks.filter(task => task.status === status).length;
    return Math.max(0, totalForStatus - onCurrentPage);
  }
}
